import { useParams } from "react-router-dom";
import Search from "../components/shared/Search";

const BookingConfirmation = () => {
    const { name } = useParams()

    return ( 
        <>
            <div className="text-center mt-5 mb-5">
                <p className="home-title">Votre rservation avec {name} est confirme !</p>
                <p className="sr-text mt-3">
                    Veuillez consulter les dtails de la rservation dans le courriel que nous vous avons envoy.
                    Il contient aussi les informations de contact du conducteur. Nhsitez pas  nous contactez 
                    si vous avez des questions !
                </p>
            </div>
            <Search isSearchPage={false}/>
        </>
     );
}
 
export default BookingConfirmation;