import { useState } from 'react';
import Search from '../components/shared/Search';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const Home = () => {
    return ( 
        <div className='flex-container'>
            <div id="home" className="flex-container3 section" style={{height: 'fit-content'}}>
                <div style={{minHeight: "17vh"}}></div>
                <img src={process.env.PUBLIC_URL + '/Images/figmaCar.png'} alt="Blue car" className="mx-auto" id="blueCar"/>
                <div className="d-block text-center mt-5">
                    <p className="mb-5 home-text">Le covoiturage par des gens de votre ville qui tiennent  vous.</p>
                    <p className="footer-title">Commencez ici !</p>
                    <a href="#how"><img src={process.env.PUBLIC_URL + '/Images/arrowBlue.jpg'} alt="blue arrow" id="arrowBlue" /></a>
                    <a className='d-block mt-5' style={{textDecorationColor: "black", fontWeight: "bold", color: "black"}} href="#search">ou cliquez ici pour sauter le tutoriel</a>
                </div>
            </div> 
            <div id="how" className="section mt-5">
                <p className="text-center home-title mt-5 mb-5">Comment a marche ?</p>
                <div className="d-flex justify-content-center">
                    <video className='mb-5' src="" controls="controls" autoPlay={true}/>
                </div>
                <ul className="progressBar mt-5">
                    <li>
                        <FontAwesomeIcon className="howIcon" icon="fa-solid fa-road" style={{color: "#1e4e98",}} />
                        <div>
                            <h4 className='mt-3 howTitle'>Rservez un trajet</h4>
                            <p className='mt-3 howText p-3'>Rserver un trajet est assez simple. Il vous suffit de: vous rendre  la section en-dessous 
                            de celle-ci et de rentrer les informations requises en commenant par le lieu de dpart, suivi
                            du lieu d'arrive, suivi de la date de dpart, suivi de l'heure de dpart et enfin de l'heure espre
                            d'arrive et ensuite de cliquez sur le boutton "C'est parti !".</p>
                            <p className='howText ps-3 pe-3'>Il vous est aussi possible d'indiquez si ceci est un trajet quotidien, c'est--dire s'il est
                            rptitif, ainsi il nous sera possible de vous montrer des conducteurs qui effectuent ce trajet rgulirement.</p>
                        </div>
                    </li>
                    <li>
                        <FontAwesomeIcon className='howIcon' icon="fa-solid fa-car" style={{color: "#1e4e98",}} />
                        <div>
                            <h4 className='mt-3 howTitle'>Choisissez un conducteur</h4>
                            <p className='howText mt-3 p-3'>
                                Une fois le trajet entr et valid, vous serez dirigez vers les rsultats correspondant  vos critres de recherche.
                                Ces rsultats peuvent tre tri avec la recherche par facettes mise  disposition et incluent les conducteurs effectuant le 
                                parcours que vous avez entr ainsi que le prix recommand pour ce parcours. Il vous suffit juste de choisir le conducteur se 
                                raprochant le plus de vos critres et votre budget. N'hsitez pas  consulter le prix recommand pour avoir une estimation 
                                de ce que vous devriez payer.
                            </p>
                            <p className='howText ps-3 pe-3'>
                                Veuillez noter que le prix recommand par la plateforme n'est qu'un prix indicatif. Il s'agit ici d'une estimation de
                                ce que vous devriez payer pour un trajet similaire au votre. Le prix affich par les conducteurs peut donc tre diffrent
                                et il varie entre +-2$ pour les petits trajets (mois de 15km) et +-5$ pour les longs trajets (plus de 15km).
                            </p>
                        </div>
                    </li>
                    <li>
                        <FontAwesomeIcon className='howIcon' icon="fa-solid fa-location-dot" style={{color: "#1e4e98",}} />
                        <div>
                            <h4 className='mt-3 howTitle'>Arrivez  destination !</h4>
                            <p className='howText mt-3 p-3'>
                                Maintenant que le choix du conducteur a t fait et valid, il sera possible de voir un message de confirmation.
                                Vous recevrez aussi un e-mail contenant les dtails du trajet aini que les coordonnes du conducteur. Prennez note
                                que la facturation est effectue seulement une fois le trajet termin.
                            </p>
                            <p className='howText ps-3 pe-3'>
                                Merci d'avoir choisi CarPoolCITY, nous esprons que vous saurez apprcier le voyage plus que la destination. Dfilez vers
                                le bas pour commencer !
                            </p>
                        </div>
                    </li>
                </ul>
            </div>
            <Search isSearchPage={false}/>
        </div>
        
     );
}
 
export default Home;